import axios from "axios";
import { useEffect, useState } from "react";
import styled from "styled-components";
import { Splide, SplideSlide } from '@splidejs/react-splide';
import '@splidejs/splide/dist/css/splide.min.css';
import { Link } from "react-router-dom";

const Popular = () => {

    const [popular, setPopular] = useState([]);

    useEffect(() => {
        getPopular();
    }, []);

    const getPopular = async () => {
        const check = localStorage.getItem('popular');

        try {

            if (check) {
                setPopular(JSON.parse(check));
                //Aqui convertimos el string almacenado en localStorage a JSON (o array en este caso)
                return;
            }
            else {
                const resp = await axios.get(`https://api.spoonacular.com/recipes/random?apiKey=${process.env.REACT_APP_API_KEY}&number=9`);

                console.log(resp.data.recipes);

                localStorage.setItem('popular', JSON.stringify(resp.data.recipes));
                //En LocalStorage solo puedes almacenar strings, por lo que convertimos el JSON o array a string.
                setPopular(resp.data.recipes);
            }

        }
        catch (error) {
            console.log(`Ocurrio un error: ${error}`);
        }
    }
    return (
        <Wrapper>
            <h3>Trending </h3>

            <Splide options={{
                perPage: 3,
                arrows: true,
                drag: 'free',
                gap: "5rem",
                pagination: false,
            }}>
                {popular.map((recipe) => (
                    <SplideSlide key={recipe.id}>
                        <Card >
                            <Link to={`/recipe/${recipe.id}`}>
                            <p>{recipe.title}</p>
                            <img src={recipe.image} alt={recipe.title} />
                            </Link>
                        </Card>
                    </SplideSlide>
                ))}
            </Splide>
        </Wrapper>
    );
}

const Wrapper = styled.div`
    margin: 4rem 0rem;
    
`;

const Card = styled.div`
    min-height: 18rem;
    border-radius: 5%;
    overflow: hidden;
    position: relative;

    

    img{
        border-radius: 5%;
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        filter: brightness(85%);
    }

    img:hover{
        transform: scale(1.05);
        transition: all 0.1s ease-in;
    }

    p{
        position: absolute;
        z-index: 10;
        left: 50%;
        bottom: 0%;
        transform: translate(-50%, 0%);
        color: white;
        text-align: center;
        width: 100%;
        font-weight: 600;
        font-size: 1rem;
        height: 40%;
        display: flex;
        justify-content: center;
        align-items: center;
    } 
`;

export default Popular;