import styled from 'styled-components';
import { useState, useEffect } from 'react';
import { FaSearch } from 'react-icons/fa';
import { AiOutlineHome } from 'react-icons/ai';
import { useNavigate, useParams, NavLink } from 'react-router-dom';
import axios from 'axios';

const Search = () => {

    const [input, setInput] = useState('');
    const navigate = useNavigate();

    const submitHandler = (e) => {
        e.preventDefault();
        console.log("Enviado");
        navigate('/searched/' + input);
    }

    return (
        <>
            <FormStyle onSubmit={submitHandler}>
            <NavLink to="/recipe-react-app/" className='home'><AiOutlineHome/></NavLink>
                <input type='text' value={input} onChange={(e) => setInput(e.target.value)} placeholder='Search your favorite recipe'/>
            </FormStyle>

        </>
    );
}

const FormStyle = styled.form`
    display:flex;
    justify-content: center;
    position: relative;
    width: 100%;
    margin: 3rem 0rem;
    
    input{
        border-radius: 42px;
        border: none;
        background: #FFF;
        box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.05) inset, 0px 4px 10px 0px rgba(0, 0, 0, 0.12);
        padding: 1rem 3rem;
        outline: none;
        width: 37.5rem;
        height: 3.6rem;
        font-size: 1.5rem;
        
    }
    .searchIcon{
        position: absolute;
        top: 1.4rem;
        left: 32rem;
    }

    .home{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 2rem;
        font-size: 2rem;
    }
`



export default Search;