import { useEffect, useState } from "react";
import styled from "styled-components";
import { useParams } from "react-router-dom";
import axios from "axios";
import {motion} from "framer-motion";

const Recipe = () => {

    let params = useParams();
    const [info, setInfo] = useState({});
    const [activeButton, setActiveButton] = useState("instructions");

    const getInfo = async () => {
        try {
            const resp = await axios.get(`https://api.spoonacular.com/recipes/${params.id}/information?apiKey=${process.env.REACT_APP_API_KEY}`);
            console.log(resp.data.title);
            setInfo(resp.data);
        }
        catch (error) {
            console.log(`Ocurrio un error: ${error}`);
        }
    }

    useEffect(() => {
        getInfo();
    }, [params.id]);


    return (
        <InfoWrapper
        animate={{opacity: 1}}
        initial={{opacity: 0}}
        exit={{opacity: 0}}
        transition={{duration: 0.2}}
        >
            <div>
                <h2>{info.title}</h2>
                <img src={info.image} alt={info.title} />
            </div>
            <Info>
                <Button className={activeButton === "instructions" ? "active" : ""} onClick={() => setActiveButton("instructions")}>Instructions</Button>
                <Button className={activeButton === "ingredients" ? "active" : ""} onClick={() => setActiveButton("ingredients")}>Ingredients</Button>
                {activeButton === "instructions" && (
                    <div>
                        <h3 dangerouslySetInnerHTML={{ __html: info.summary }}></h3>
                        <h3 dangerouslySetInnerHTML={{ __html: info.instructions }}></h3>
                    </div>
                )}

                {activeButton === "ingredients" && (
                    <ul>
                        {info.extendedIngredients.map((ingredient) => (
                            <li key={ingredient.id}>{ingredient.original}</li>
                        ))}
                    </ul>
                )}


            </Info>
        </InfoWrapper>
    );
}

const InfoWrapper = styled(motion.div)`
margin-top: 10rem;
margin-bottom: 5rem;
display: flex;

.active{
    background: linear-gradient(35deg, #494949, #313131);
    color: white;
}

h2, h3 {
    margin-bottom: 2rem;
}

h3 {
    font-size: 1.3rem;
    line-height: 1.5rem;
    font-weight: 400;
}

li {
    font-size: 1.3rem;
    line-height: 2.5 rem;
}

ul {
    margin-top: 2rem;
}
`
const Button = styled.button`
    padding: 1rem 2rem;
    color: #313131;
    background: white;
    border: 2px solid black;
    margin-right: 2rem;
    margin-bottom: 2rem;
    font-weight: 600;
    &:hover{
        cursor: pointer;
    }
`

const Info = styled.div`
margin-left: 10rem;
`

export default Recipe;