import { useEffect, useState } from "react";
import styled from "styled-components";
import { motion } from 'framer-motion';
import { useParams, Link } from 'react-router-dom';
import axios from 'axios';

const Searched = () => {

    const [cuisine, setCuisine] = useState([]);
    const params = useParams();

    const getCuisine = async (name) => {

        try {

            const resp = await axios.get(`https://api.spoonacular.com/recipes/complexSearch?apiKey=${process.env.REACT_APP_API_KEY}&query=${name}&number=12`);

            console.log(resp.data.results.length);
            if(resp.data.results.length === 0) {
                setCuisine(["No results found"]);
            } else {
                setCuisine(resp.data.results);
        }
            }
            
        catch (error) {
            console.log(`Ocurrio un error: ${error}`);
        }
    }

    const displayResults = (cuisine) => {

        if(cuisine[0] === "No results found") {
            return <h3>No results found</h3>
        }
            return cuisine.map((recipe) => (
            <Card key={recipe.id}>
                <Link to={`/recipe/${recipe.id}`}>
                <p>{recipe.title}</p>
                <img src={recipe.image} alt={recipe.title} />
                </Link>
            </Card>
        ))
    }


    useEffect(() => {
        getCuisine(params.search);
    }, [params]);

    return (
        <>
            <h3>Results</h3>
            <Grid
            animate={{opacity: 1}}
            initial={{opacity: 0}}
            exit={{opacity: 0}}
            transition={{duration: 0.5}}>
                {displayResults(cuisine)}
            </Grid>
        </>
    );
}

const Grid = styled(motion.div)`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 3rem;
    margin: 2rem 0;
    
`;

const Card = styled.div`
    min-height: 16rem;
    border-radius: 5%;
    overflow: hidden;
    position: relative;

    img{
        border-radius: 5%;
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        filter: brightness(85%);
    }

    img:hover{
        transform: scale(1.05);
        transition: all 0.1s ease-in;
    }

    p{
        position: absolute;
        z-index: 10;
        left: 50%;
        bottom: 0%;
        transform: translate(-50%, 0%);
        color: white;
        text-align: center;
        width: 100%;
        font-weight: 600;
        font-size: 1rem;
        height: 40%;
        display: flex;
        justify-content: center;
        align-items: center;
    } 
`;

export default Searched;