import styled from "styled-components";
import { Splide, SplideSlide } from '@splidejs/react-splide';
import { NavLink } from 'react-router-dom';
import italian from '../assets/italian.jpg';
import african from '../assets/african.jpg';
import american from '../assets/american.jpg';
import japanese from '../assets/japanese.jpg';
import spanish from '../assets/spanish.jpg';
import thai from '../assets/thai.jpg';
import mexican from '../assets/mexican.jpg';
import indian from '../assets/indian.jpg';


const Categories = () => {

    return (
        <Wrapper>
        {/* <h2>What are your favorite cuisines?</h2> */}
        <List>
            <Splide options={{
                perPage: 8,
                arrows: true,
                drag: 'free',
                pagination: false,
                width: '100%'
            }}>
                <SplideSlide>
                    <Category to={'cuisine/Italian'} >

                        <h4>Italian</h4>
                        <img src={italian} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/American'}>

                        <h4>American</h4>
                        <img src={american} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Thai'}>

                        <h4>Thai</h4>
                        <img src={thai} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Japanese'}>

                        <h4>Japanese</h4>
                        <img src={japanese} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/African'}>

                        <h4>African</h4>
                        <img src={african} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Mexican'}>

                        <h4>Mexican</h4>
                        <img src={mexican} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Indian'}>

                        <h4>Indian</h4>
                        <img src={indian} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Spanish'}>

                        <h4>Spanish</h4>
                        <img src={spanish} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Italian'}>

                        <h4>Italian</h4>
                        <img src={italian} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/American'}>

                        <h4>American</h4>
                        <img src={american} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Thai'}>

                        <h4>Thai</h4>
                        <img src={thai} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Japanese'}>

                        <h4>Japanese</h4>
                        <img src={japanese} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/African'}>

                        <h4>African</h4>
                        <img src={african} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Mexican'}>

                        <h4>Mexican</h4>
                        <img src={mexican} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Indian'}>

                        <h4>Indian</h4>
                        <img src={indian} alt='' />
                    </Category>
                </SplideSlide>

                <SplideSlide>
                    <Category to={'cuisine/Spanish'}>

                        <h4>Spanish</h4>
                        <img src={spanish} alt='' />
                    </Category>
                </SplideSlide>
            </Splide>
        </List>
        </Wrapper>
    );
}


const Wrapper = styled.div`
h2{
    text-align: center;
    font-weight: 700;
    margin-bottom: 2rem;
}`
const List = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-around;
`
const Category = styled(NavLink)`
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-radius: 50%;
    width: 8rem;
    height: 8rem;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    position: relative;
    &:hover{
        filter: brightness(120%);
        transition: all 0.3s ease-in-out;
    }

    
    img{
        border-radius: 2rem;
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        filter: brightness(60%);
    }


    h4{
        position: absolute;
        z-index: 10;
        left: 50%;
        bottom: 0%;
        transform: translate(-50%, 0%);
        color: white;
        text-align: center;
        width: 100%;
        font-size: 0.8rem;
        font-weight: 400;
        height: 90%;
        display: flex;
        justify-content: center;
        align-items: center;
    } 
`


export default Categories;

